h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $heading-font;
  color: var(--heading-color);
  font-weight: 800;
  margin: 0;
}
h1,
.h1 {
  font-size: 90px;
  line-height: 1.05em;
  letter-spacing: -1px;
  @include max(1199px) { 
    font-size: 65px;
  }
 
  @include max(767px) { 
    font-size: 48px;
  }
}

h2,
.h2 {
  font-size: 70px;
  line-height: 1em; /* 116.667% */
  letter-spacing: -.1px;
  @include max (991px){
    font-size: 48px;
  }
  @include max (767px){
    font-size: 36px;
  }
}
h3,
.h3 {
  font-size: 35px!important;
  line-height: 1.12em;
  @include max (1199px){
    font-size: 24px!important;
  }
  
}
h4,
.h4 {
  font-size: 24px!important;
  line-height: 1.17em;
  font-weight: 700;
  @include max (767px){
    font-size: 20px!important;
  }
  
}
h5,
.h5 {
  font-size: 24px;
  line-height: 28px;
  font-weight: 700;
}



p{  
  &:last-child{
    margin-bottom: 0;
  }
}


@for $i from 10 through 40 {
  .f-size-#{$i} {
    font-size: $i * 1px !important;
  }
}

@for $i from 10 through 40 {
  .line-height-#{$i} {
    line-height: $i * 1px !important;
  }
}

.font-bold{
  font-weight: 700;
}
.font-black{
  font-weight: 800;
}
.font-medium{
  font-weight: 500;
}
.font-normal{
  font-weight: 400;
}
.font-semibold{
  font-weight: 600;
}


