.uxora-contact{
    &-box{
        padding: 30px;
        margin-left: 50px;
        background: var(--white-bg);
        border-radius: 10px;
        h3{
            padding-bottom: 30px;
        }
        p{
            font-weight: 600;
            margin-bottom: 10px;
        }
        @include max (1199px){
            margin-left: 30px;
        }
        @include max (991px){
            margin-left: 0;
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
        &.box2{
            max-width: 636px;
            margin: 0 auto 0;
            margin-top: 50px;
            @include max (991px){
                margin-top: 40px;
            }
            @include max (767px){
                margin-top: 30px;
            }
            h2{
                text-align: center;
            }

        }
    }
    &-info-wrap{
        display: flex;
        align-items: center;
        gap: 24px;
        border: 2px solid var(--heading-color);
        padding: 42px 30px;
        border-radius: 20px;
        margin-bottom: 24px;
        &.wrap2{
            padding: 26px 30px;
        }
    }
    &-us-map{
        width: 100%;
        border: 2px solid var(--heading-color);
        border-radius: 10px;
        height: 750px;
        @include max (991px){
            height: 400px;
        }
    }
}
