.uxora-listicon{
    &-wrap{
        ul{
            li{
                position: relative;
                padding-left: 55px;
                padding-bottom: 24px;
                &:last-child{
                    padding-bottom: 0;
                }
                img{
                    position: absolute;
                    top: 5px;
                    left: 0;
                }
            }
        }
    }
    &-wrap2{
        ul{
            li{
                position: relative;
                padding-left: 20px;
                padding-bottom: 24px;
                @include max (767px){
                    padding-bottom: 15px;
                }
                &:last-child{
                    padding-bottom: 0;
                }
                &::before{
                    content: "";
                    position: absolute;
                    top: 10px;
                    left: 0;
                    width: 8px;
                    height: 8px;
                    border-radius: 100px;
                    background-color: var(--heading-color);
                }
            }
        }
    }
}