.uxora-section-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.uxora-section-padding2{
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}
.uxora-section-padding3{
    padding: 0 0 106px;
    @include max (991px){
        padding: 0 0 76px;
    }
    @include max (767px){
        padding: 0 0 36px;
    }
}
.uxora-section-padding4{
    padding: 0 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}
.uxora-section-padding5{
    padding: 195px 0 130px;
    @include max (991px){
        padding: 150px 100px;
    }
    @include max (767px){
        padding:130px 0 80px;
    }
}
.uxora-section-padding6{
    padding: 130px 0 80px;
    @include max (991px){
        padding: 80px 0 30px;
    }
    @include max (767px){
        padding:60px 0 10px;
    }
}







// section title

.uxora-section-title{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-660{
        max-width: 660px;
    }
    &.max-700{
        max-width: 750px;
        @include max(991px) {
            max-width: 550px;
            margin: 0 auto;
        }
        
    }
   
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
            @include max(991px) {
                max-width: 550px;
            }
        }
        &.max-width-750{
            max-width: 750px;
            @include max(991px) {
                max-width: 600px;
            }
            
        }
        &.max-width-800{
            max-width: 800px;
        }
    }
   
}
// default content
.uxora-default-content{
    h2{
        padding-bottom: 20px;
    }
    h3{
        padding-bottom: 15px;
    }
    .data{
        font-size: 21px;
        line-height: 30px;
    }
    &.content2{
        max-width: 700px;
        padding-top: 130px;
        @include max (991px){
            padding-top: 80px;
        }
        @include max (767px){
            padding-top: 60px;
        }
    }
    &.content3{
        padding-left: 48px;
        @include max (1199px){
            padding-left: 30px;
        }
        @include max (767px){
            padding-left: 0;
        }
        p a{
            font-weight: 600;
            display: block;
            color: var(--heading-color);
        }
    }
}
.pb-80{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
        @include max (767px){
            padding-bottom: 40px;
        }
    }
}
.pl-110{
    padding-left: 110px;
    @include max (1199px){
        padding-left: 50px;
    }
    @include max (991px){
