.uxora-hero{
  &-section{
    padding: 190px 0 130px;
    position: relative;
    @include max (1199px){
      padding: 190px 0 130px;
    }
    @include max (991px){
      padding: 190px 0 70px;
    }
    @include max (767px){
      padding: 130px 0 70px;
    }
  }
  &-rating{
    padding-bottom: 24px;
    ul{
      li{
        display: flex;
        align-items: center;
        gap: 10px;
        position: relative;
        padding-left: 40px;
        img{
          position: absolute;
          top: -5px;
          left: 0;
        }
        h6{
          font-weight: 700;
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
  }
  &-content{
    max-width: 636px;
    @include max (1199px){
      padding-right: 30px;
    }
    @include max (767px){
      padding-right: 0px;
    }
    p{
      font-size: 21px;
      line-height: 30px;
    }
  }
  &-dashbord{
    @include max (991px){
      padding-top: 40px;
    }
    @include max (767px){
      padding-top: 30px;
    }
  }
}
.uxora_screenfix_right {
  margin-right: calc((-100vw + 100%) / 2);
}
