.uxora-iconbox{
  &-wrap{
    padding: 40px 30px;
    border-radius: 10px;
    transition: all .4s;
    position: relative;
    margin-bottom: 24px;
    background-color: var(--heading-bg2);
    @include max (767px){
      padding: 30px;
    }
  }
  &-icon{
    padding-bottom: 24px;
      
  }
  &-content{
    padding-bottom: 24px;
    h3{
      padding-bottom: 20px;
      color: var(--light-color);
    }
    p{
      color: var(--light-color);
    }
    &.content2{
      h3,p {
        color: var(--heading-color);
        max-width: 350px;
      }
    }
  }
  &-btn{
    width: 40px;
    height: 40px;
    border-radius: 7px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--heading-bg2);
    --arrow-hover-move-x: -110%;
    position: relative;
    transition: all 0.4s;
    a{
      padding: 5px 10px;
      background-color: var(--heading-color);
    }
    &:hover{
      path{
        fill: var(--accent-color);
      }
    }
  }
  // end
  &-wrap2{
    padding: 30px;
    display: flex;
    gap: 24px;
    border-radius: 20px;
    margin-bottom: 24px;
    background-color: #FDF0E71A;
  }
  &-number{
    h2{
      color: var(--light-color);
    }
  }
  &-content2{
    h3{
      color: var(--light-color);
      padding-bottom: 20px;
    }
    p{
      color: var(--light-color);
    }
  }
  // end two
  &-wrap3{
    text-align: center;
    @include max (767px){
      margin-bottom: 24px;
    }
  }
  &-icon3{
    padding-bottom: 24px;
  }
  &-content3{
    h3{
      padding-bottom: 20px;
    }
  }
  // end three
  &-wrap4{
    padding: 40px;
    margin-bottom: 24px;
    @include max (1199px){
      padding: 20px;
    }
    @include max (991px){
      padding: 10px 0;
    }
  }
  &-icon{
    padding-bottom: 24px;
      
  }
  &-content4{
    h3{
      padding-bottom: 20px;
      color: var(--light-color);
    }
    p{
      color: var(--light-color);
    }
  }
  // end four
  &-wrap5{
    margin-bottom: 50px;
  }
  &-icon2{
    width: 100%;
    height: 180px;
    border-radius: 10px;
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--white-bg);
  }
}





.light-color{
  color: var(--light-color);
}
.uxora-iconbox-btn svg {
  transition: all 0.3s ease-out 0s;
}
.uxora-iconbox-btn:hover svg path:nth-of-type(1), .uxora-iconbox-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(calc(-1* var(--arrow-hover-move-x)));
  opacity: 0;
  transition-delay: 0s, 0s;
}
.uxora-iconbox-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}
.uxora-iconbox-btn:hover svg path:nth-of-type(2), .uxora-iconbox-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
.uxora-iconbox-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}


