.uxora-faq{
  &-item{
    position: relative;
    cursor: pointer;
    padding-bottom: 24px;
    border-bottom: 1px solid #0A0F0A1A;
    margin-bottom: 24px;
    &.open {
      position: relative;
      .uxora-faq-header{
        padding-bottom: 15px;
      }
      .uxora-faq-body{
        display: block;
        
      }
    }
    &.item2{
      padding-bottom: 0;
      border: none;
      padding-left: 15px;
      &.open{
        &::after{
          opacity: 1;
        }
      }
      &::before{
        width: 4px;
        height: 100%;
        border-radius: 7px;
        top: 0;
        left: 0;
        content: "";
        position: absolute;
        background-color: var(--heading-color);
      }
      &::after{
        width: 4px;
        height: 100%;
        border-radius: 0;
        top: 0;
        left: 0;
        content: "";
        position: absolute;
        opacity: 0;
        background-color: var(--accent-color);
      }
    }
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-body{
    display: none;
    max-width: 850px;
  }
}
.uxora-active-icon
.uxora-faq-item.open {
  img.plasicon{
    opacity: 1!important;
  }
  img.mynusicon{
    opacity: 0;
    display: block;
  }
}
.border-b{
  padding-bottom: 0;
  border-bottom: none;
  margin-bottom: 0;
}

img.mynusicon {
  opacity: 1;
  position: relative;
}
img.plasicon {
  opacity: 0;
  position: absolute;
}
.uxora-active-icon{
  flex-shrink: 0;
  img{
    @include max (767px){
      width: 40px;
    }
  }
}

