.uxora-cta{
    &-section{
        padding: 130px 0;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
    }
    &-content{
        max-width: 700px;
        margin: 0 auto;
        text-align: center;
        @include max(991px) {
            max-width: 650px;
        }
        h2{
            color: var(--light-color);
        }
    }
    &-data{
        padding-top: 20px;
        ul{
            li{
                display: inline-block;
                margin-right: 20px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    font-size: 16px;
                    line-height: 24px;
                    color: var(--light-color);
                    display: flex;
                    align-items: center;
                    gap: 10px;
                }
            }
        }
    }
    // end
}
.ml-15{
    margin-left: 15px;
    @include max (767px){
        margin-left: 0;
        margin-top: 24px;
    }
}