.uxora-team{
    &-wrap{
        padding: 40px;
        border-radius: 10px;
        background-color: var(--white-bg);
        transition: all 0.4s;
        position: relative;
        margin: 0 12px;
        &:hover{
            .uxora-team{
                &-thumb img{
                    transform: scale(1.1);
                }
            }
            
        }
    }
    &-rating{
        padding-bottom: 24px;
    }
    &-text{
        font-weight: 600;
        font-style: italic;
        padding-bottom: 24px;
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        margin-bottom: 24px;
        border-radius: 10px;
        img{
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            object-fit: cover;
            border-radius: 10px;
        }
    }
    &-author{
        p{
            margin: 0;
            font-size: 24px;
            font-weight: 600;
            line-height: 28px;
            color: var(--heading-color);
        }
        span{
            padding-top: 5px;
        }
    }
}

.uxora-team-slider-init .uxora-team-slider-next, 
.uxora-team-slider-init .uxora-team-slider-prev {
    top: 47%;
    right: 10px;
    width: 40px;
    height: 40px;
    z-index: 1;
    display: flex;
    cursor: pointer;
    position: absolute;
    transition: all .4s;
    align-items: center;
    border-radius: 7px;
    background-size: 20px;
    justify-content: center;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    transform: rotate(180deg) !important;
    background-color: var(--heading-bg);
    background-image: url(../images/team/arrow.svg);
    @include max (1399px){
        top: 55%;
    }
    @include max (767px){
        display: none!important;
    }
}
.uxora-team-slider-init .uxora-team-slider-prev {
    left: 10px;
    top: 47%;
    transform: rotate(0deg)!important;
    background-image: url(../images/team/arrow.svg);
    @include max (1399px){
       top: 55%;
    }
}
