
.uxora-brand{
    &-section{
        padding-top: 100px;
        @include max (991px){
            padding-top: 80px;
        }
        @include max (767px){
            padding-top: 60px;
        }
        &.section2{
            padding: 0;
        }
    }
    &-title{
        text-align: center;
        padding-bottom: 60px;
        @include max (767px){
            padding-bottom: 40px;
        }
        p{
            font-size: 24px;
            line-height: 28px;
            font-weight: 700;
        }
    }
}
