.uxora-pricing{
    &-wrap{
        padding: 10px;
        border-radius: 10px;
        transition: all .4s!important;
        margin-bottom: 24px;
        position: relative;
        margin-top: 50px;
        background-color: var(--white-bg);
        &.wrap2{
            @include max (767px){
                margin-top: 50px;
            }
        }
        @include max (991px){
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 0;
        }
    }
    &-header-top{
        padding: 20px 15px;
        border-radius: 10px;
        margin-bottom: 24px;
        background: linear-gradient(180deg, rgba(250, 255, 165, 0) 0%, rgba(148, 242, 127, 0.5) 100%);
    }
    &-header{
        padding-bottom: 24px;
    }
    &-price{
        gap: 5px;
        display: flex;
        align-items: flex-end;
        padding-bottom: 24px;
    }
    &-body{
        padding: 10px 10px 0;
        h4{
            padding-bottom: 24px;
        }
        ul{
            li{
                position: relative;
                padding-bottom: 10px;
                padding-left: 20px;
                &::before{
                    top: 10px;
                    left: 0;
                    width: 10px;
                    height: 10px;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--accent-bg);
                }
                &:last-child{
                    padding-bottom: 0;
                }
            }
        }
    }
    &-footer{
        padding: 0 20px 20px 20px;
    }
    &-tag{
        display: flex;
        align-items: center;
        gap: 5px;
        background-color: var(--accent-bg);
        border-radius: 10px 10px 0 0;
        position: absolute;
        top: -48px;
        left: 0;
        width: 100%;
        height: 55px;
        justify-content: center;
        z-index: -1;
    }
    // end 
}
.pb-50{
    h2{
        padding-bottom: 50px!important;
        @include max (767px){
            padding-bottom: 30px!important;
        }
    }
}

.ux-price-value.dynamic-value {
    font-family: $heading-font;
    font-size: 70px;
    font-weight: 700;
    line-height: 1em;
    letter-spacing: -1px;
}

.toggle-btn{
    @include max (767px){
        padding: 10px 0 10px 10px;
    }
}

.pricing-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    @include max (767px){
        display: block;
    }
    label {
        margin: 0 20px;
        font-size: 18px;
        line-height: 30px;
        color: var(--heading-color);
        font-weight: 700;
        @include max(767px) {
        margin: 0 10px;
        font-size: 16px;
        }
        span{
            font-weight: 600;
        }
    }
}

[data-pricing-dynamic][data-value-active="yearly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-yearly);
  }
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] {
  background: #ff5722;
  }
  
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] span {
  left: calc(100% - 33px);
  }
  [data-pricing-dynamic][data-value-active="monthly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-monthly);
  }
  .dynamic-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .static-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .pricing-btn .btn-toggle {
    position: relative;
    width: 70px !important;
    height: 35px;
    border-radius: 50px;
    border: none;
    margin: 0;
    padding: 0!important;
    cursor: pointer;
    background-size: 25px;
    border: none!important;
    background-position: center right 5px;
    border: none !important;
    background-position: center right 5px;
    background-color: var(--heading-color);
    background-image: url(../images/shape/switcher-shape.svg) !important;
}
 
.form-check-input:focus {
    border-color: none;
    outline: 0;
    box-shadow:none;
}
.pricing-btn .btn-toggle:checked {
    background-position: center left 5px;
    background-image: url(../images/shape/switcher-shape.svg) !important;
}
