// fonts variable
$body-font: "Inter", serif!default;
$heading-font: "Cabinet Grotesk", serif!default;

// color variable
:root {
  --heading-color: #0A0F0A;
  --heading-bg: #0A0F0A;
  --heading-bg2: #161B16;
  --accent-color: #94F27F;
  --accent-bg: #94F27F;
  --accent-bg2: #8D1BF3;
  --white-color: #ffffff;
  --white-bg: #ffffff;
  --light-color: #F5F9F5;

}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 1.5em;
$buton_border_radius: 10px;
$button_padding: 16px 45px;
$button_font_weight: 700;
$button_font_family: $body-font;