input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 145px;
  resize: none;
  &::placeholder{
   color: var(--heading-color)!important;
   }
  
}
.uxora-main{
   &-field{
      position: relative;
      input{
         width: 300px;
         padding: 18px 24px;
         border-radius: 10px;
         font-size: 16px;
         line-height: 24px;
         border-radius: 3px;
         background-color: var(--white-bg);
         &::placeholder{
            color: #0A0F0A;
         }
         @include max (767px){
            width: 100%;
         }
      }
      &.field2{
         padding-bottom: 24px;
         p{
            font-weight: 700;
            margin-bottom: 10px;
         }
         input{
            width: 100%;
            background-color: var(--light-color);
            &::placeholder{
               color: #0A0F0A99;
            }
         }
      }
      &-textarea{
         padding: 20px;
         background-color: var(--light-color);
         p{
            font-weight: 600;
            margin-bottom: 10px;
         }
      }
   }

  
}

.uxora-form-box{
   input {
      padding: 0;
      height: initial;
      width: initial;
      margin-bottom: 0;
      display: none;
      cursor: pointer;
   }
}
.uxora-form-box {
   padding-top: 24px;
   label {
      position: relative;
      cursor: pointer;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
   }
}
.uxora-form-box label::before {
   content: "";
   background-color: transparent;
   border: 2px solid rgba(0, 6, 40, .6);
   padding: 7px;
   display: inline-block;
   position: relative;
   vertical-align: middle;
   cursor: pointer;
   margin-right: 12px;
   margin-top: -2px;
}
.uxora-form-box input:checked+label:after {
   content: "";
   display: block;
   position: absolute;
   top: 7px;
   left: 6px;
   width: 5px;
   height: 9px;
   border: 1px solid var(--heading-color);
   border-width: 0 2px 2px 0;
   transform: rotate(45deg);
}

.field-icon {
   float: right;
   margin-right: 25px;
   margin-top: -38px;
   position: relative;
   z-index: 2;
   cursor: pointer;
}
.forgot-password {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
   padding-bottom: 24px;
   a{
      font-size: 16px;
      line-height: 24px;
      color: var(--heading-color);
      @include max (767px){
         margin-top: 10px;
      }
   }
   label{
      span{
         color: var(--heading-color);
         font-weight: 600;
      }
   }
}
.form-bottom-text{
   margin-top: 10px;
   text-align: center;
   p{
      line-height: 24px;
      font-size: 16px;
      font-weight: 400;
      a{
         font-weight: 600;
         color: var(--heading-color);
      }
   }
}