
.uxora-default-btn,
.uxora-subcribe-btn,
#uxora-submit-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  border-radius: $buton_border_radius;
  width: fit-content;
  position: relative;
  z-index: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-flex;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  overflow: hidden;
  border: 2px solid var(--accent-color);
  color: var(--heading-color);
  background-color: var(--accent-bg);
  overflow: hidden;
  transform-style: preserve-3d;
  @include max (767px){
    padding: 18px 30px;
  }
  &.btn2{
    &::before{
      top: 23px;
      left: 23px;
      width: 10px;
      height: 10px;
      content: "";
      position: absolute;
      border-radius: 100px;
      background-color: var(--heading-bg);
      @include max (767px){
        top: 25px;
        left: 12px;
      }
    }
  }
  &.btn3{
    &::before{
      left: 220px;
      @include max (767px){
        left: 150px;
      }
      @include max (490px){
        left: 85px;
      }
    }
    &::after{
      width: 600px;
      @include max (767px){
        width: 460px;
      }
      @include max (500px){
        width: 400px;
      }
    }
  }
  &.btn4{
    &::before{
      left: 200px;
      @include max (991px){
        left: 180px;
      }
      @include max (767px){
        left: 150px;
      }
      @include max (490px){
        left: 85px;
      }
    }
    &::after{
      width: 600px;
      @include max (767px){
        width: 460px;
      }
      @include max (500px){
        width: 400px;
      }
    }
  }
  &.btn5{
    &::before{
      @include max (767px){
        left: 170px;
      }
      @include max (530px){
        left: 150px;
      }
      @include max (490px){
        left: 130px;
      }
      @include max (440px){
        left: 110px;
      }
      @include max (400px){
        left: 90px;
      }
    }
  }
  &.sub-btn{
    &:hover{
      border: 2px solid var(--white-bg)!important;
    }
    @include max (767px){
      width: 100%;
      justify-content: center;
    }
    &::after{
      width: 230px;
      background-color: var(--white-bg);
      @include max (767px){
        width: 500px;
      }
      
      @include max (400px){
        width: 350px;
      }
    }
  }
  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 0.5s, border 0.5s;
  }
  &::after{
    content: "";
    position: absolute;
    top: 1%;
    left: 0%;
    width: 300px;
    height: 300px;
    background-color: var(--light-color);
    border-color: transparent;
    border-radius: 50%;
    transform: translate(-10px, -70px) scale(0.1);
    opacity: 0;
    z-index: -1;
    transition: transform 0.5s, opacity 0.5s, background-color 0.5s;
  }
  &:hover{
    color: var(--heading-color)!important;
    border: 2px solid var(--heading-color);
    &::after {
      opacity: 1;
      border: 2px solid var(--accent-bg);
      transform: scaleX(1.5) scaleY(1.5);
    }
  }

  &.uxora-header-btn{
    padding: 11px 38px;
    &::before{
      top: 18px;
      left: 18px;
      width: 10px;
      height: 10px;
      content: "";
      position: absolute;
      border-radius: 100px;
      background-color: var(--heading-bg);
    }
    @include max (767px){
      display: none;
    }
    &::after{
      width: 200px;
    }
  }
  &.uxora-pricing-btn{
    &::before{
      top: 23px;
      left: 40px;
      width: 10px;
      height: 10px;
      content: "";
      position: absolute;
      border-radius: 100px;
      background-color: var(--heading-bg);
      @include max (767px){
        top: 25px;
        left: 12px;
      }
    }
    &::after{
      @include max (1399px){
        width: 550px;
      }
      @include max (991px){
        width: 300px;
      }
      @include max (767px){
        width: 550px;
      }
      @include max (500px){
        width: 400px;
      }
      @include max (400px){
        width: 300px;
      }
    }
  }
 





  &.d-block {
    width: 100%;
  }
  &:focus {
    box-shadow: none;
  }
}
.hero-btn-wrap{
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.uxora-title-btn {
  @include max (991px){
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
  &.btn2{
    display: flex;
    align-items: center;
    gap: 24px;
    flex-wrap: wrap;
  }
}


// add animation

.title-btn svg {
  transition: all 0.3s ease-out 0s;
  margin-left: 5px;
}
.title-btn:hover svg path:nth-of-type(1), .title-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(calc(-1* var(--arrow-hover-move-x)));
  opacity: 0;
  transition-delay: 0s, 0s;
}
.title-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.title-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}
.title-btn:hover svg path:nth-of-type(2), .title-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.title-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
.title-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.title-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}



.title-btn{
  user-select: none;
  -moz-user-select: none;
  background: var(--white-bg) none repeat scroll 0 0;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  text-align: center;
  touch-action: manipulation;
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
  vertical-align: middle;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -o-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
  white-space: nowrap;
  overflow: hidden;
  border-radius: 50px;
  --arrow-hover-move-x: -110%;
  gap: 10px;
  z-index: 1;
  position: relative;
  border: none;
}