.uxora-process{
  &-content{
    @include max (991px){
      padding-bottom: 40px;
    }
    @include max (767px){
      padding-bottom: 30px;
    }
    max-width: 660px;
    h2,p{
      color: var(--light-color);
    }
  }
}
// add single
.uxora-service-d{
  &-wrap {
    margin: 0 auto;
    text-align: center;
    max-width: 1076px;
  }
  &-thumb{
    padding-bottom: 40px;
    position: relative;
    @include max (767px){
      padding-bottom: 30px;
    }
  }
  &-content{
    text-align: left;
    padding-bottom: 24px;
    max-width: 856px;
    h3{
      padding-bottom: 20px;
    }
    ul{
      li{
        font-weight: 600;
        padding-left: 20px;
        padding-bottom: 5px;
        position: relative;
        &:last-child{
          padding-bottom: 0;
        }
        span{
          font-weight: 400;
          font-size: 18px;
          line-height: 30px;
        }
        &::before{
          position: absolute;
          top: 10px;
          left: 0;
          width: 8px;
          height: 8px;
          content: "";
          border-radius: 100px;
          background-color: var(--heading-bg);
        }
      }
    }
  }
}


.mt-40{
  margin-top: 40px;
  @include max (767px){
    margin-top: 30px;
  }
}

// add integratios

.uxora-integration-d{
  &-wrap{
    padding: 40px;
    border-radius: 10px;
    max-width: 850px;
    margin: 0 auto;
    background-color: var(--white-bg);
  }
  &-title{
    padding-bottom: 80px;
    @include max (991px){
      padding-bottom: 60px;
    }
    @include max (767px){
      padding-bottom: 40px;
    }
    ul{
      display: flex;
      align-items: center;
      gap: 40px;
      justify-content: center;
      li{
        a{
          width: 100px;
          height: 100px;
          border-radius: 5px;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: var(--white-bg);
        }
      }
    }
  }
}
.pb-30{
  padding-bottom: 30px;
}