.uxora-footer{
  &-section{
    padding-top: 130px;
    @include max (991px){
      padding-top: 80px;
    }
    @include max (767px){
      padding-top: 60px;
    }
  }
  &-menu{
    h4{
      margin-bottom: 24px;
    }
    ul{
      li{
        color: var(--heading-color);
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          font-size: 21px;
          line-height: 30px;
          color: var(--heading-color);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--heading-color);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      padding-left: 110px;
      @include max (1199px){
        padding-left: 0;
      }
      @include max (767px){
        padding-left: 0;
        padding-top: 40px;
      }
    }
    
  }
  &-textarea{
    img{
      padding-bottom: 24px;
    }
    p{
      font-size: 21px;
      font-weight: 400;
      line-height: 30px;
    }
    @include max (1399px){
      margin-bottom: 40px;
      max-width: 500px;
    }
   
  }
  &-bottom-wrap{
    margin-top: 80px;
    border-top: 1px solid #0A0F0A1A;
    padding: 35px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include max (767px){
      display: block;
      text-align: center;
    }
    @include max (991px){
      margin-top: 80px;
    }
    @include max (767px){
      margin-top: 60px;
    }
  }
  &-bottom-text{
    p{
      @include max (767px){
        margin-bottom: 15px;
        
      }
    }
  }
}
.pl-100{
  padding-left: 100px;
  @include max (1399px){
    padding-left: 0;
  }

}
.pl-30{
  padding-left: 30px;
  @include max (1199px){
    padding-left: 0;
  }
  @include max (767px){
    padding-top: 40px;
  }
}