.uxora-t{
    &-icon{
        padding-bottom: 24px;
        img{
            margin: 0 auto;
        }
    }
    &-content{
        padding-bottom: 30px;
        h3{
            color: var(--light-color);
        }
    }
    &-author{
        img{
            margin: 0 auto
        }
        p{
            margin-top: 10px;
            font-size: 16px;
            line-height: 24px;
            color: var(--light-color);
        }
    }
  
}
.uxora-t-slider-init {
    max-width: 1074px;
    text-align: center;
    margin: 0 auto;
    height: 370px;
    @include max (991px){
        height: 450px;
    }
    @include max (767px){
        height: 400px;
    }
    @include max (520px){
        height: 430px;
    }
    @include max (400px){
        height: 460px;
    }
}
.ptt-0{
    @include max (991px){
        padding-top: 0!important;
    }
}